import React from 'react';

import './index.css';
const MenuItem = ({children, icon, inrow, click})=>{
  return (
      <div className = {inrow? 'menuitem-row menuitem' : 'menuitem'} onClick = { click }>
        <div className={ icon }></div>
        <div>{children}</div>
      </div>
    )
}

export default MenuItem;


