import React from 'react';
// import Base64 from '../../codeHandler.js';

import './index.css';


const Chapter = ({title, list = [], style, toRead, lastChapter}) => {
  return(<div className = "chapter" >
          <div className = "lastChapter">
            { lastChapter ? <div>上次读到第{parseInt(lastChapter)+1}章</div> : ''}
          </div>
          <h3>{ title }</h3>
          <div className = "chapter_list">
            <ul onClick = { (e) => { toRead(e )} } >
              { list.map( ( item ) => {
                return(
                    <li key = { item.chapter_id } ><a href="" style = { style } data-id = { item.chapter_id } >{ item.title }</a></li>
                  )
              })}
            </ul>
          </div>
        </div>
  )
}

const Words = ( {words, preChapter, nextChapter } )=>{
  // console.log( words);
    let wordsShower;
    return(
      <div ref = { (node) => wordsShower = node} >
        <h3>{words.t}</h3>
        <div>
          { words.p.map( (item) => {
            return <p>{item}</p>
          })}
        </div>
        <div className = "page-control">
          <button onClick = { () => { preChapter(); wordsShower.scrollTo(0, 0) }}>上一章</button>
          <button onClick = { () => { nextChapter(); wordsShower.scrollTo(0, 0) }}>下一章</button>
        </div>
      </div>
    );

}


class Content extends React.Component{
  constructor( props ){
    super( props );
    this.state = {
      words : {'t':'','p':[]},
      page : localStorage.getItem( 'page') || 0,
    }
    this.getWords = this.getWords.bind(this);
    this.toRead = this.toRead.bind(this);
    this.prevChapter = this.prevChapter.bind(this);
    this.nextChapter = this.nextChapter.bind(this);
    this.setPage = this.setPage.bind(this);
  }

  componentWillReceiveProps(nextProps){
    localStorage.setItem( 'fontSize', nextProps.fontSize);
  }

  prevChapter(){
    // console.log('pre');
    const currentPage = parseInt(this.state.page);
    if( currentPage <= 0){
      this.setState({
          words : {'t':'提示','p':['已经是第一章了']}
        });
      return ;
    }
    this.getWords( currentPage - 1 );
    this.setPage ( currentPage - 1 );
    // this.content.scrollTo(0,0);
    // console.log(currentPage);
  }

  nextChapter(){
    console.log('next');
    const currentPage = parseInt(this.state.page);
    if( currentPage >= this.props.chapter.chapters.length){
      this.setState({
          words : {'t':'提示','p':['已经完结了']}
        });
      return ;
    }
    this.getWords( currentPage + 1 );
    this.setPage ( currentPage + 1 );
    // this.content.scrollTo(0,0);
    // console.log(currentPage);
  }



  toRead( e ){
    if( e.target.tagName === 'A' ){
      this.content.scrollTo(0,0);
    // e.target.preventDefault();
     e.preventDefault();
    const id = e.target.dataset.id ;
    this.props.setIsChapter( false );
    localStorage.setItem( 'page', id );
    this.setPage( id )
    this.getWords( id );
    }
  }

  setPage( page ){
    this.setState({
      page
    })
  }

  getWords( id ){

    //获取数据
    fetch(`./data/data${id}.json`)
    // fetch('./data/data0.json')
    .then( (response) => response.json() )
    // .then( (result) => result.jsonp)
    // .then( (jsonp) => {
    //   const script = document.createElement('script');
    //   script.setAttribute( 'src', jsonp );
    //   document.querySelector('body').appendChild(script);
    //   window.jsonp.on( 'getJsonp',( data )=>{
    //     this.setState({
    //       words : JSON.parse(Base64.atob( data , 'base64'))
    //     })
    //     console.log( Base64.atob( data , 'base64') )
    //   })
    // })
    .then( (result) => {
      this.setState({
          words : result
          });
    })
    .catch( () => {
          this.setState({
          words : {'t':'提示','p':['暂时不提供此章节']}
          });
          localStorage.setItem( 'page' , id);
        })
    // .then( (words) => {
    //   // console.log(words);
    //   this.setState({
    //     words : JSON.parse(words)
    //   });

    //   // this.props.setHash({'chapter' : false});
    // } )

  }

  toggleControlWithTest( e, fn ){
    // console.log(e.target.tagName);
    const tagName = String(e.target.tagName);
    if( tagName !== 'A' && tagName !=='BUTTON' ){
      fn();
    }
  }

  render(){
    const {bgcolor, fontSize, hash, chapter, setHash, isChapter, toggleControl, hideControl} = this.props;
    const { words , page  } = this.state;

    // console.log(words);
    // let isChapter = true;
    const style= {
                'backgroundColor' : bgcolor,
                'fontSize' : `${fontSize}px`
              };

    return(
      <div style = { style }
           className = "content" onClick = { (e)=>{ this.toggleControlWithTest( e, toggleControl)}}
           onScroll = { hideControl }
           ref = { (node) => { this.content = node }}
           >
        { isChapter
          ? <Chapter title = { chapter.title }
                     list = { chapter.chapters}
                     toRead = { this.toRead }
                     lastChapter = { page }
                     >
            </Chapter>
          : <Words words = { words }
                   preChapter = { this.prevChapter }
                   nextChapter = { this.nextChapter }
            >
            </Words> }
      </div>
    );
  }
  // componentDidUpdate(){
  //   if( this.props.isChapter ){
  //     this.content.scrollTo(0, 0);
  //   }

  //   // console.log( this.content );
  // }
}


export default Content;


