import React from 'react';

import './index.css';

import MenuItem from '../MenuItem' ;


class Foot extends React.Component{
  constructor({props}){
    super(props);
    this.state = {
      isShowSetting : false
    }
    this.showSetting = this.showSetting.bind(this);
    this.hideSetting = this.hideSetting.bind(this);
  }

  componentWillReceiveProps(nextProps){
    // console.log('updata');
    // console.log(nextProps);
    if(nextProps.isShow === false){
      console.log('-----------------false');
      this.hideSetting();
    }
  }

  showSetting( ){
    // console.log('show setting');
    if(this.state.showSetting){
      return ;
    }
    this.setState({
      isShowSetting : true
    })
  }

  hideSetting(  ){
    // console.log('im hiding setting');
    // console.log(this.state.isShowSetting);
    if(!this.state.isShowSetting){
      return ;
    }
    this.setState({
      isShowSetting : false
    })
  }

  render(){
    const { isShowSetting } = this.state;
    const { isShow, changeBgcolor, changeFontSize, setHash, setIsChapter } = this.props;
    return(
      <div className = { isShow?"foot foot-show":"foot"} >
        <div className="menu">
          <MenuItem inrow = {true} icon = { 'glyphicon glyphicon-list' } click = { ()=>{ setIsChapter(true)} }>目录</MenuItem>
          <MenuItem inrow = {true} icon = { 'glyphicon glyphicon-font' } click = { this.showSetting } >字体</MenuItem>
          <MenuItem inrow = {true} icon = { 'glyphicon glyphicon-lamp' } click = {() => { changeBgcolor('#000') }}>夜间</MenuItem>
        </div>
        <div className = { isShowSetting?"setting setting-show":"setting" }>
          <div>
            <label htmlFor="">字号:</label>
            <button onClick = { () => {changeFontSize('increase')} } >大</button>
            <button onClick = { () => {changeFontSize('decrease')} } >小</button>
          </div>
          <div>
            <label htmlFor="">背景:</label>
            <div className="btn-group-color">
              <button className = "btn-color" onClick = { () => { changeBgcolor('#FFC299') }}></button>
              <button className = "btn-color" onClick = { () => { changeBgcolor('#85CFE8') }}></button>
              <button className = "btn-color" onClick = { () => { changeBgcolor('#9785E8') }}></button>
              <button className = "btn-color" onClick = { () => { changeBgcolor('#EE8AC2') }}></button>
              <button className = "btn-color" onClick = { () => { changeBgcolor('#000') }}></button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


export default Foot;


