import React from 'react';

import './index.css';
import {ButtonGroup, Button} from 'react-bootstrap';

const Head = ({children, click, isShow}) => {
  return (

      <div onClick = { click } className = { isShow?"head head-show":"head"}>
        <span className="glyphicon glyphicon-chevron-left"></span>
        <span>{children}</span>
      </div>
    )
}

export default Head;


