import React, { Component } from 'react';
import './index.css';


import Head from '../Head';
import Content from '../Content';
// import Chapter from '../Chapter';
import Foot from '../Foot';


class App extends Component {
  constructor(){
    super();
    this.state = {
      isShowControl : false,
        bgcolor : localStorage.getItem('bgcolor') || '#FFC299',
        fontSize : localStorage.getItem('fontSize') || 14,
        chapter : {},
        hash : '',
        isChapter : true

    }
    this.toggleControl = this.toggleControl.bind(this);
    this.hideControl = this.hideControl.bind(this);
    this.changeBgcolor = this.changeBgcolor.bind(this);
    this.changeFontSize = this.changeFontSize.bind(this);
    // this.onChangehash = this.onChangehash.bind(this);
    // this.setHash = this.setHash.bind(this);
    this.setIsChapter = this.setIsChapter.bind(this);
  }

  toggleControl(){
    const newState = !this.state.isShowControl;
    this.setState({
        isShowControl : newState
      });
  }

  hideControl(){
    if(!this.state.isShowControl){
      return;
    }
    this.setState({
        isShowControl : false
      });
  }

  changeBgcolor(color){
    this.setState({
      bgcolor : color
    });
    localStorage.setItem('bgcolor', color);
  }

  changeFontSize( type ){
    console.log(type);
    const fontSize = this.state.fontSize;
    switch ( type ) {
      case 'increase' :{
        if( fontSize >= 30 ){
          return;
        }
        this.setState((prevState) => {
          return prevState.fontSize++;
        });
        break;
      }
      case 'decrease' :{
        if( fontSize <= 14 ){
          return ;
        }
        this.setState((prevState) => {
          return prevState.fontSize--;
        });
        break;
      }
      default : {
        return false;
      }
    }
    // localStorage.setItem('fontSize' , this.state.fontSize);
  }

  // onChangehash(){
  //   this.setState({
  //     hash : window.location.hash
  //   })
  // }

  // setHash( hash_map ){
  //   let hash = '';
  //   for(let key in hash_map){
  //     hash += `${key}=${hash_map[key]}&`;
  //   }
  //   window.location.hash = hash.replace(/&$/, '');
  // }

  setIsChapter( bool ){
    this.setState({
      isChapter : bool,
      isShowControl : false
    });
    // window.scrollTo(0,0);
  }

  render() {
    const { isShowControl, bgcolor, fontSize, chapter, hash, isChapter} = this.state;
    return (
      <div className="App">
        <Head isShow = { isShowControl }>返回书架</Head>
        <Content bgcolor = { bgcolor }
                 fontSize = { fontSize }
                 hash = { hash }
                 chapter = { chapter }
                 isChapter = { isChapter }
                 setIsChapter = { this.setIsChapter }
                 toggleControl = { this.toggleControl}
                 hideControl = { this.hideControl }
                 >
        </Content>
        <Foot isShow = { isShowControl }
              changeBgcolor = { this.changeBgcolor }
              changeFontSize = { this.changeFontSize }
              setIsChapter = { this.setIsChapter }
              >
        </Foot>

      </div>
    );
  }
  componentDidMount(){
    fetch('./data/chapter.json')
    .then((response)=> response.json())
    .then((result)=>{
      // console.log(result);
      this.setState({
        chapter : result
      })
    })
    // window.addEventListener('scroll',this.hideControl);
    // window.addEventListener('hashchange', this.onChangehash);
    // window.document.addEventListener( 'click', this.toggleControl)
  }
}

export default App;

// <div className = "active-control" onClick = { this.toggleControl} style = { isChapter?{"display" : 'none'}:{"display" : 'block'} }></div>


